/**
 * Toolbar Component -  3.0 + Projects Management
 *  LoginDialog,       
 * +    ProjectsDialog
 */

import React, { useState, useEffect } from 'react';
// Router navigation removed - using window.location instead
import { 
  Save, 
  FolderOpen, 
  Undo2, 
  Redo2, 
  ZoomIn, 
  ZoomOut, 
  Grid, 
  Magnet,
  Play,
  Package,
  User,
  LogOut
} from 'lucide-react';
import { useEditorStore } from '../stores/editorStore';
import { apiClient } from '../services/api-client';
import { logger } from '../utils/logger';
import AutoSaveIndicator from './AutoSaveIndicator';
import ProjectsDialog from './ProjectsDialog';
import BuildDialog from './BuildDialog';
import './Toolbar.css';

export const Toolbar: React.FC = () => {

  const {
    project,
    history,
    zoom,
    gridEnabled,
    snapToGrid,
    undo,
    redo,
    setZoom,
    toggleGrid,
    toggleSnapToGrid,
    saveProject
  } = useEditorStore();
  // Preview functionality
  const { project: projectForPreview, savePreviewSnapshot } = useEditorStore();
  const [previewLoading, setPreviewLoading] = React.useState(false);


  // User info
  const [organizationName, setOrganizationName] = useState<string | null>(null);
  const [plan, setPlan] = useState<string | null>(null);
  const [showProjectsDialog, setShowProjectsDialog] = useState(false);
  const [showBuildDialog, setShowBuildDialog] = useState(false);

  useEffect(() => {
    const orgData = apiClient.getOrganizationData();
    setOrganizationName(orgData.name);
    setPlan(orgData.plan);
  }, []);

  const handleZoomIn = () => {
    setZoom(Math.min(zoom + 0.1, 3));
  };

  const handleZoomOut = () => {
    setZoom(Math.max(zoom - 0.1, 0.1));
  };

  const handleLogout = () => {
    if (confirm('    ?')) {
      logger.info('User initiated logout');
      apiClient.logout();
      window.location.href = "/login";
    }
  };

  const handlePreview = async () => {
    const proj = projectForPreview || project;
    if (!proj) {
      alert('    ');
      return;
    }

    setPreviewLoading(true);

    try {
      console.log('[Toolbar] Starting preview...');
      const snapshotId = await savePreviewSnapshot();
      window.open(`/preview?projectId=${snapshotId}`, '_blank');
      console.log('[Toolbar] Preview opened');
    } catch (error: any) {
      console.error('[Toolbar] Error:', error);
      alert(`: ${error.message}`);
    } finally {
      setPreviewLoading(false);
    }
  };

  const canUndo = history.past.length > 0;
  const canRedo = history.future.length > 0;

  return (
    <>
      <div className="toolbar">
        <div className="toolbar-section">
          {/* Logo */}
          <div className="toolbar-logo">
            <span className="logo-text">Kiosk Editor</span>
          </div>

          <div className="toolbar-divider" />

          {/* File operations */}
          <button 
            className="toolbar-btn" 
            onClick={() => saveProject()}
            disabled={!project}
            title=" (Ctrl+S)"
          >
            <Save size={18} />
          </button>

          <button 
            className="toolbar-btn" 
            onClick={() => setShowProjectsDialog(true)}
            disabled={!project}
            title=" "
          >
            <FolderOpen size={18} />
          </button>

          <div className="toolbar-divider" />

          {/* History */}
          <button 
            className="toolbar-btn" 
            onClick={undo}
            disabled={!canUndo}
            title=" (Ctrl+Z)"
          >
            <Undo2 size={18} />
          </button>

          <button 
            className="toolbar-btn" 
            onClick={redo}
            disabled={!canRedo}
            title=" (Ctrl+Shift+Z)"
          >
            <Redo2 size={18} />
          </button>

          <div className="toolbar-divider" />

          {/* Zoom */}
          <button 
            className="toolbar-btn" 
            onClick={handleZoomOut}
            disabled={zoom <= 0.1}
            title=" (Ctrl+-)"
          >
            <ZoomOut size={18} />
          </button>

          <span className="toolbar-zoom">{Math.round(zoom * 100)}%</span>

          <button 
            className="toolbar-btn" 
            onClick={handleZoomIn}
            disabled={zoom >= 3}
            title=" (Ctrl++)"
          >
            <ZoomIn size={18} />
          </button>

          <div className="toolbar-divider" />

          {/* Grid */}
          <button 
            className={`toolbar-btn ${gridEnabled ? 'active' : ''}`}
            onClick={toggleGrid}
            title=" "
          >
            <Grid size={18} />
          </button>

          <button 
            className={`toolbar-btn ${snapToGrid ? 'active' : ''}`}
            onClick={toggleSnapToGrid}
            title="  "
          >
            <Magnet size={18} />
          </button>
        </div>

        {/* Right section */}
        <div className="toolbar-section">
          {/* AutoSave Indicator */}
          {project && <AutoSaveIndicator />}

          <div className="toolbar-divider" />

          {/* User info */}
          {organizationName && (
            <div className="toolbar-user-info">
              <User size={16} />
              <span className="user-org">{organizationName}</span>
              {plan && <span className="user-plan">{plan}</span>}
            </div>
          )}

          {/* Logout */}
          <button 
            className="toolbar-btn logout-btn"
            onClick={handleLogout}
            title=""
          >
            <LogOut size={18} />
          </button>

          <div className="toolbar-divider" />

          {/* Build Player */}
          <button 
            className="toolbar-btn"
            disabled={!project}
            onClick={() => setShowBuildDialog(true)}
            title="  Windows"
          >
            <Package size={18} />
          </button>

          {/* Preview */}
          <button 
            className="toolbar-btn preview-btn" 
            disabled={!project || previewLoading}
            onClick={handlePreview}
            title=""
          >
            <Play size={18} />
            <span>{previewLoading ? '...' : ''}</span>
          </button>
        </div>
      </div>

      {/* Projects Dialog */}
      {showProjectsDialog && (
        <ProjectsDialog
          onClose={() => setShowProjectsDialog(false)}
        />
      )}

      {/* Build Dialog */}
      {showBuildDialog && (
        <BuildDialog
          onClose={() => setShowBuildDialog(false)}
        />
      )}
    </>
  );
};

export default Toolbar;
