import React, { useEffect, useState, useRef } from 'react';
import { Image as KonvaImage, Rect, Group, Circle, Ellipse, RegularPolygon, Star, Line } from 'react-konva';
import { Widget } from '../types';
import { useEditorStore } from '../stores/editorStore';
import ClippedImage from './ClippedImage';
import ImageGallery from './ImageGallery';

interface ImageWidgetProps {
  widget: Widget;
  isSelected: boolean;
  onSelect: (e: any) => void;
  onDragEnd: (e: any) => void;
  onTransformEnd: (e: any) => void;
  dragBoundFunc?: (pos: { x: number; y: number }) => { x: number; y: number };
}

const ImageWidget: React.FC<ImageWidgetProps> = ({
  widget,
  isSelected,
  onSelect,
  onDragEnd,
  onTransformEnd,
  dragBoundFunc
}) => {
  //    -  ImageGallery
  if (widget.properties.galleryMode) {
    return (
      <ImageGallery
        widget={widget}
        isSelected={isSelected}
        onSelect={onSelect}
        onDragEnd={onDragEnd}
        onTransformEnd={onTransformEnd}
        dragBoundFunc={dragBoundFunc}
      />
    );
  }

  //     
  const [image, setImage] = useState<HTMLImageElement | null>(null);
  const groupRef = useRef<any>(null);
  
  const { clipShape = 'rectangle' } = widget.properties;

  useEffect(() => {
    if (!widget.properties.src) {
      setImage(null);
      return;
    }

    const img = new window.Image();
    img.crossOrigin = 'anonymous';
    
    img.onload = () => {
      setImage(img);
    };
    
    img.onerror = () => {
      console.error('Failed to load image:', widget.properties.src);
      setImage(null);
    };
    
    img.src = widget.properties.src;

    return () => {
      img.onload = null;
      img.onerror = null;
    };
  }, [widget.properties.src]);

  //   -      
  useEffect(() => {
    if (!image || widget.properties.objectFit !== 'adaptive') return;

    const { updateWidget } = useEditorStore.getState();
    const imgRatio = image.width / image.height;
    const currentRatio = widget.width / widget.height;

    // ,    
    //       
    if (Math.abs(imgRatio - currentRatio) > 0.01) {
      //  ,  
      const newHeight = widget.width / imgRatio;
      
      updateWidget(widget.id, {
        height: Math.round(newHeight)
      });
    }
  }, [image, widget.properties.objectFit, widget.width, widget.height, widget.id]);

  //       objectFit
  const getImageProps = () => {
    if (!image) return null;

    const { width, height } = widget;
    const { objectFit = 'contain' } = widget.properties;
    const imgRatio = image.width / image.height;
    const widgetRatio = width / height;

    let cropX = 0;
    let cropY = 0;
    let cropWidth = image.width;
    let cropHeight = image.height;

    switch (objectFit) {
      case 'cover': {
        //   ,  
        if (imgRatio > widgetRatio) {
          //    -   
          cropWidth = image.height * widgetRatio;
          cropX = (image.width - cropWidth) / 2;
        } else {
          //    -  /
          cropHeight = image.width / widgetRatio;
          cropY = (image.height - cropHeight) / 2;
        }
        break;
      }
      case 'contain':
        //      
        //   canvas 
        break;
      case 'fill':
        //    ,  
        break;
      case 'scale-down': {
        //  :  ,   
        //    contain,      -  
        if (image.width <= width && image.height <= height) {
          //    -   
          cropWidth = image.width;
          cropHeight = image.height;
        } else {
          //   -    ( contain)
          // crop  
        }
        break;
      }
      case 'adaptive':
        //      useEffect
        break;
    }

    return {
      cropX,
      cropY,
      cropWidth,
      cropHeight
    };
  };

  const imageProps = getImageProps();
  const { borderEnabled, borderStyle, borderWidth = 2, borderColor = '#000000' } = widget.properties;

  //  borderStyle  strokeDash
  const getStrokeDash = (style: string) => {
    switch (style) {
      case 'dashed':
        return [10, 5];
      case 'dotted':
        return [2, 2];
      case 'double':
        return [];
      default:
        return [];
    }
  };

  const handleTransform = (e: any) => {
    const node = groupRef.current;
    if (!node) return;

    //   scale  -     Canvas.tsx
    onTransformEnd(e);
  };

  const isLocked = widget.locked || false;
  //     clipShape
  //       CSS clip-path  PreviewPage/Player
  const getShapePoints = (shape: string, w: number, h: number): number[] | null => {
    const cx = w / 2;
    const cy = h / 2;
    const r = Math.min(w, h) / 2;

    const polygonPoints = (sides: number, startAngle: number = -Math.PI / 2): number[] => {
      const pts: number[] = [];
      for (let i = 0; i < sides; i++) {
        const angle = startAngle + (2 * Math.PI * i) / sides;
        pts.push(cx + r * Math.cos(angle));
        pts.push(cy + r * Math.sin(angle));
      }
      return pts;
    };

    switch (shape) {
      case 'triangle': {
        const angles = [-Math.PI / 2, Math.PI / 6, 5 * Math.PI / 6];
        const pts: number[] = [];
        angles.forEach(a => {
          pts.push(cx + r * Math.cos(a));
          pts.push(cy + r * Math.sin(a));
        });
        return pts;
      }
      case 'diamond':
        return [cx, 0, w, cy, cx, h, 0, cy];
      case 'pentagon':
        return polygonPoints(5);
      case 'hexagon':
        return polygonPoints(6);
      case 'octagon':
        return polygonPoints(8);
      case 'star': {
        const pts: number[] = [];
        for (let i = 0; i < 10; i++) {
          const angle = (i * Math.PI / 5) - Math.PI / 2;
          const sr = i % 2 === 0 ? r : r * 0.5;
          pts.push(cx + sr * Math.cos(angle));
          pts.push(cy + sr * Math.sin(angle));
        }
        return pts;
      }
      default:
        return null;
    }
  };

  const renderBorder = () => {
    if (!borderEnabled) return null;

    const strokeProps: any = {
      stroke: borderColor,
      strokeWidth: borderWidth,
      dash: getStrokeDash(borderStyle),
      fill: undefined,
      listening: false,
    };

    const shape = clipShape || 'rectangle';
    const w = widget.width;
    const h = widget.height;
    const r = Math.min(w, h) / 2;
    const cx = w / 2;
    const cy = h / 2;

    // ,    Line (   clip-path)
    const points = getShapePoints(shape, w, h);
    if (points) {
      return <Line points={points} closed stroke={borderColor} strokeWidth={borderWidth} dash={getStrokeDash(borderStyle)} listening={false} />;
    }

    //     Konva-
    switch (shape) {
      case 'circle':
        return <Circle x={cx} y={cy} radius={r} {...strokeProps} />;

      case 'ellipse':
        return <Ellipse x={cx} y={cy} radiusX={w / 2} radiusY={h / 2} {...strokeProps} />;

      case 'rounded-rectangle':
        return <Rect width={w} height={h} cornerRadius={widget.properties.cornerRadius || 20} {...strokeProps} />;

      case 'rectangle':
      default:
        return <Rect width={w} height={h} {...strokeProps} />;
    }
  };


  return (
    <Group
      ref={groupRef}
      id={widget.id}
      x={widget.x}
      y={widget.y}
      width={widget.width}
      height={widget.height}
      rotation={widget.rotation || 0}
      draggable={!isLocked}
      dragBoundFunc={dragBoundFunc}
      opacity={isLocked ? 0.6 : 1}
      onClick={onSelect}
      onTap={onSelect}
      onDragEnd={onDragEnd}
      onTransformEnd={handleTransform}
    >
      {/*      */}
      <Rect
        width={widget.width}
        height={widget.height}
        fill="transparent"
        listening={true}
      />

      {/*  (   ) */}
      {!image && (
        <Rect
          width={widget.width}
          height={widget.height}
          fill="#9b59b6"
          listening={false}
        />
      )}

      {/*  */}
      {image && (
        clipShape && clipShape !== 'rectangle' ? (
          <ClippedImage widget={widget} image={image} />
        ) : (
          imageProps && (
            <KonvaImage
              image={image}
              x={0}
              y={0}
              width={widget.width}
              height={widget.height}
              crop={{
                x: imageProps.cropX,
                y: imageProps.cropY,
                width: imageProps.cropWidth,
                height: imageProps.cropHeight
              }}
              listening={false}
            />
          )
        )
      )}

      {/*   */}
      {isSelected && (
        <Rect
          width={widget.width}
          height={widget.height}
          stroke="#007acc"
          strokeWidth={2}
          listening={false}
        />
      )}

      {/*   (  clipShape) */}
      {renderBorder()}
    </Group>
  );
};

export default ImageWidget;
